body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.central.slide .slide-content {
  display:-ms-flexbox;
  display:flex;
}

.slide-content > * {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  z-index:1;
  position:relative;
}

.slide-content > h1, .full {
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
}

.central.slide p {
  padding-left:5%;
}

span.bold {
  font-weight:bold;
}

span.underline {
  text-decoration: underline;
}

p.full {
  text-align:center;
}

.white {
  color:white;
}

.nobullet {
  list-style-type: none;
}

.slide-content img.right-bottom {
  position:absolute;
  bottom:0px;
  right:0px;
  z-index:0;
}

.huge {
  font-size: 144pt;
  text-align:center;
}

/* The WCAG mapping table */

.wcag-map {
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 6px;
}

.wcag-map .os {
  height: 1px;
  width: 1px;
  overflow: hidden;
  position: absolute;
}

table.wcag-map {
  border-collapse: collapse;
}
table.wcag-map th:not(:first-child) {
  width: 40px;
}
table.wcag-map th[scope='row'] {
  text-align: left;
  font-weight: normal;
}
table.wcag-map th,
table.wcag-map td {
  border-bottom: 1px solid #eee;
  padding: 0.2em;
}
table.wcag-map td .data-cell {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
table.wcag-map td .data-cell .dot {
  background-color: #008000;
  border-radius: 50%;
}
table.wcag-map td .data-cell .dot.strong {
  height: 7px;
  width: 7px;
}
table.wcag-map td .data-cell .dot.moderate {
  height: 5px;
  width: 5px;
  opacity: 0.8;
}
table.wcag-map td .data-cell .dot.weak {
  height: 3px;
  width: 3px;
  opacity: 0.6;
}



